import React, { useState, useEffect, useRef } from 'react';
import { ApolloClient, InMemoryCache, gql } from '@apollo/client';
import chroma from 'chroma-js';

// Setup Apollo Client
const client = new ApolloClient({
    uri: 'https://api.github.com/graphql',
    cache: new InMemoryCache(),
    headers: {
        Authorization: `Bearer ${process.env.REACT_APP_GITHUB_TOKEN}`,
    },
});

const GET_CONTRIBUTIONS = gql`
  query getContributions($username: String!) {
    user(login: $username) {
      contributionsCollection {
        contributionCalendar {
          weeks {
            contributionDays {
              date
              contributionCount
            }
          }
        }
      }
    }
  }
`;

const GitHubCalendar = ({ username }) => {
    const [contributions, setContributions] = useState(null);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);
    const containerRef = useRef(null); // Reference to the container for dynamic width calculation

    // Fetch the contributions data from GitHub using Apollo Client
    useEffect(() => {
        client
            .query({
                query: GET_CONTRIBUTIONS,
                variables: { username },
            })
            .then((result) => {
                setContributions(result.data.user.contributionsCollection.contributionCalendar);
                setLoading(false);
            })
            .catch((err) => {
                console.error('Error fetching data:', err);
                setError(err);
                setLoading(false);
            });
    }, [username]);

    // Render loading or error state
    if (loading) return <div>Loading contributions...</div>;
    if (error) return <div>Error fetching data. Please try again later.</div>;

    // Function to determine the color based on contribution count
    const getColorForContributions = (count) => {
        // if (count === 0) return '#ebedf0'; // No contributions
        // if (count <= 5) return '#c6e48b'; // Low contributions (light green)
        // if (count <= 20) return '#7bc96f'; // Medium contributions (green)
        // return '#239a3b'; // High contributions (dark green)
        const contribution_cap = 20;
        const capped_count = Math.min(contribution_cap, count);
        const percent = capped_count <= 0 ? 0 : capped_count / contribution_cap;
        // const color1 = "#696969";
        // const color2 = "#FFBBFF";
        const scale = chroma.scale(["#2b2b2b", "#00ffb1"]);
        return scale(percent);
        // return count >= 5 ? "#FFFFFF" : "#000000"; 
    };

    // Calculate width for each week based on available space
    const calculateWeekWidth = () => {
        if (containerRef.current) {
            const containerWidth = containerRef.current.offsetWidth; // Get available width of container
            const numberOfWeeks = contributions?.weeks.length || 1; // Number of weeks in the calendar
            const weekWidth = containerWidth / numberOfWeeks; // Distribute the width equally

            // Minimum width for each week to avoid it being too small
            return Math.max(weekWidth, 40); // Set a minimum width (e.g., 40px)
        }
        return 0;
    };

    return (
        <div>
            <h3>{username}'s GitHub Contributions Calendar</h3>
            <div
                ref={containerRef} // Reference to the container
                style={{
                    display: 'flex',
                    flexDirection: 'row',  // Stack weeks horizontally
                    gap: '0px',            // Adjusted gap between weeks to 1px
                    width: '100%',         // Take up full available width
                    flexWrap: 'nowrap',    // Don't wrap to a new row
                    justifyContent: 'center',
                }}
            >
                {/* Iterate over the weeks */}
                {contributions?.weeks.map((week, weekIndex) => (
                    <div
                        key={weekIndex}
                        style={{
                            display: 'flex',
                            flexDirection: 'column',   // Stack days vertically
                            flexShrink: 1,             // Prevent weeks from shrinking
                            alignItems: 'center',
                            width: `${calculateWeekWidth()}px`, // Calculate width dynamically
                        }}
                    >
                        {/* Iterate over the days in the week */}
                        {week.contributionDays.map((day, dayIndex) => (
                            <div
                                key={dayIndex}
                                style={{
                                    width: '18px',
                                    height: '18px',
                                    backgroundColor: getColorForContributions(day.contributionCount),
                                    borderRadius: '3px',
                                    margin: '1px', // Margin between each day
                                    display: 'inline-block',
                                }}
                            />
                        ))}
                    </div>
                ))}
            </div>
        </div>
    );
};

export default GitHubCalendar;
